@font-face {
  font-family: 'RODEO';
  src: local('RODEO'), url(/aws-client/static/media/RODEO.a2d41863.ttf) format('truetype');
}
@font-face {
  font-family: 'PressStart';
  src: local('PressStart'), url(/aws-client/static/media/PressStart.81c0aae9.ttf) format('truetype');
