body {
  margin: 0;
  padding: 0;
  background: black;
  font-family: "PressStart", sans-serif;
  font-size: 16px;
  color: #333;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#root {
  height:99vh;
  overflow:auto;
}


button, input {
  font-family: "PressStart" !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.News {
  font-family: "News", sans-serif;
  font-size: 300%;
}

h1, h2, h3, h4, h5, h6 {
  font-family: "RODEO", serif;
}

#city_scape {
  -webkit-animation: SkyColor 60s ease infinite;
          animation: SkyColor 60s ease infinite;
}

@-webkit-keyframes SkyColor {
  0% {-webkit-filter: hue-rotate(0deg);filter: hue-rotate(0deg);}
  50% {-webkit-filter: hue-rotate(180deg);filter: hue-rotate(180deg);}
  0% {-webkit-filter: hue-rotate(360deg);filter: hue-rotate(360deg);}
}

@keyframes SkyColor {
  0% {-webkit-filter: hue-rotate(0deg);filter: hue-rotate(0deg);}
  50% {-webkit-filter: hue-rotate(180deg);filter: hue-rotate(180deg);}
  0% {-webkit-filter: hue-rotate(360deg);filter: hue-rotate(360deg);}
}

select.form-control,
textarea.form-control,
input.form-control {
  font-size: 16px;
}
input[type=file] {
  width: 100%;
}


.ui.checkbox label {
    color: white !important;
}

.ui.checkbox label:hover {
    color: grey !important;
}
